.App {
    text-align: center;
    display: flex;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    box-sizing: border-box;
    flex-direction: column;
    background-color: #111111;
}

.intro {
    display: flex;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1000;
    flex-direction: column;
    background-color: #ffffff;
    justify-content: center;
    align-items: center;
}

.keyboard {
    display: flex;
    height: 200px;
    background-color: #111111;
    width: 100%;
    position: fixed;
    right: 0;
    bottom: 0;
    left: 0;
    flex-wrap: wrap;
    border-top: 1px solid #111111;
    border-left: 1px solid #111111;
    border-right: none;
    border-bottom: none;
    justify-content: flex-start;
}

.keyboard-letter {
    width: 10%;
    border-bottom: 1px solid #111111;
    border-right: 1px solid #111111;
    border-left: none;
    border-top: none;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 16px;
    transition: all ease-in-out 0.3s;
    color: #ffffff;
}

.key {
    background-color: #242424;
    color: #999999;
}
.key:hover {
    background-color: #444444;
    color: #111111;
}

.key-right {
    background-color: green;
    color: #ffffff;
}
.key-right:hover {
    background-color: darkgreen;
    color: #ffffff;
}
.keyboard-letter:focus {
    background-color: red;
    color: #ffffff;
}

.word {
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    right: 0;
    bottom: 300px;
    left: 0;
    color: #ffffff;

}

.word-letter {
    font-size: 20px;
    line-height: 30px;
    width: 5vw;
    border-bottom: 1px solid #545454;
    margin: 0 5px;
}

.hidden {
    opacity: 0;
}

.visible {
    opacity: 1;
}

.statebox {
    display: flex;
    flex-direction: column;
    height: 300px;
    justify-content: space-around;
    align-items: center;
}

.box {
    width: 80vw;
    font-size: 20px;
    line-height: 30px;
    color: #ffffff;
}

.reset {
    font-size: 15px;
    padding: 11px 30px 13px;
    border-radius: 23px;
    border: 1px solid rgba(255, 255, 255, 0.3);
    color: #ffffff;
    background-color: transparent;
    transition: all ease-in-out 0.5s;
}
.reset:hover {
    background-color: #ffffff;
    color: #111111;
    border-color: rgba(255, 255, 255, 1);
}

.box,
.intro h1 {
    color: #ffffff;
    font-size: 50px;
    margin: 0;
    line-height: 1.3;
}

.intro p {
    color: rgba(255, 255, 255, 0.7);
    margin-bottom: 40px;
}
